import React from 'react';
import { Collapse, Divider } from 'antd';
import '../styles/faqs.css';


const items = [
  {
    key: '1',
    label: 'What Destinations Do You Offer Tours To?',
    children: <p>{`
    Embark on a journey of discovery as we guide you through a selection of breathtaking destinations that span the globe. Whether you're a culture enthusiast, an adrenaline junkie, a nature lover, or a history buff, we have a tour that will captivate your imagination and leave you with unforgettable memories
    `}</p>,
  },
  {
    key: '2',
    label: ' What Types of Travel Experiences Do You Specialize In?',
    children: <p>{`
    We specialize in crafting unique travel experiences that cater to your interests, passions, and desires. Whether you're seeking cultural immersion, thrilling adventures, luxurious getaways, wellness retreats, historical exploration, culinary delights, family bonding, solo escapades, romantic retreats, or nature photography, we're here to create your dream journey.
    `}</p>,
  },
  {
    key: '3',
    label: 'What Sets Your Tours Apart from Others?',
    children: <p>{`
    At Demicod, we stand out by curating unparalleled journeys that fuse authenticity, expertise, and innovation. Our tours are meticulously designed to immerse you in the heart of each destination, providing a unique blend of insight, adventure, and comfort.
    `}</p>,
  },
  {
    key: '4',
    label: 'How Do You Ensure the Safety and Well-being of Travelers?',
    children: <p>{`
    We are committed to ensuring your safety and well-being throughout your journey. Our stringent measures and dedicated focus on traveler welfare provide you with complete peace of mind as you explore the world with us.
    `}</p>,
  },
  {
    key: '5',
    label: 'What Is the Booking Process for Your Tours?',
    children: <p>{`
    Booking your tour with Demicod is a smooth and straightforward process. We ensure user-friendly online reservations
Transparent and detailed itineraries.Timely confirmation for your convenience.Embark on your adventure effortlessly with us.
    `}</p>,
  },
  {
    key: '6',
    label: 'Do You Offer Travel Insurance Options?',
    children: <p>{`
    Yes, at Demicod, we provide comprehensive travel insurance options for your peace of mind. Your safety and security are our utmost concern.
    `}</p>,
  },
];



const Faqs = () => {
    const onChange = (key) => {
      console.log(key);
    }

    return (
      <div className='faq-container'>
        {
          items.map((e,index)=>(
            <Collapse
            size="large"
            items={[e]}
            className='questions-container'
            key={index}
            />
          ))
        }
      </div>
    )
  
  };
export default Faqs