import React from 'react';
import { Carousel } from 'antd';
import { Reviews } from '../data/whyChooseUsData';
import image1 from '../assets/images/quote-left-solid.svg';
import '../styles/whyChooseUs.css';


const contentStyle = {
  height: '300px',
  color: '#000',
  lineHeight: '160px',
  textAlign: 'center',
  background: '#87ceeb'
};

const WhyChooseUs = () => {
  return(
    <div className="WhyChooseUs-container">

      <div className='WhyChooseUs-container-left'>
        <p>Best Travel Guide</p>
        <h3><span>Why</span> Choose Us?</h3>
        <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. Separated they live in 
          Bookmarksgrove right at the coast of the Semantics, a large language ocean.
        </p>
        <p>Even the all-powerful Pointing has no control about the blind texts it is an almost unorthographic life.</p>
        <div className='read-more-btn'>Read more</div>
      </div>

      <div className='WhyChooseUs-container-right'>
        <p>Testimony</p>
        <h3><span>Our</span> Customers Says</h3>
        <div className='WhyChooseUs-container-right-review'>
          <img src={image1} alt='comment Logo' />
  
            <Carousel autoplay>
                
                  { Reviews.map(e=>(
                    <div style={contentStyle}>
                      <p className='whyChoose-us-text'>{e.review}</p>
                      <br />
                      <h2>{e.name}</h2>
                      <p>{e.occupation}</p>
                    </div>
                  ))}
            </Carousel>

        </div>
      </div>
    </div>
  )
}

export default WhyChooseUs;