import PagesTopSection from "../components/pages-topsection";
import Footer from "../components/footer";
import image from '../assets/images/contact-image.jpg'
import '../styles/contact.css';


const google_addr = "https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d31518.284535028593!2d7.474380799999999!3d9.0832896!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sng!4v1691406734873!5m2!1sen!2sng"

const Contact = () => {
  return(
  <>
      <PagesTopSection heading={"Contact Us"} image= {image} />
      <div className="contact-container">
        <h2>Contact Information</h2>
        <div className="contact-information">

          <div className="contact-address">
            <p>Address: No. 1 Kandi Close, Wuse 2, Abuja, Nigeria</p>
          </div>

          <div>
            <p>Phone: +234805099530</p>
          </div>

          <div>
            <p>Email: info@demmicodtravels.com</p>
          </div>

          <div>
            <p>Email: info@demmicodtravels.com</p>
          </div>

          <div>
            <p>Website: demmicod-travels.com</p>
          </div>
        </div>

          <div className="contact-form">
            <div className="contact-form-left">
              <input type="text" placeholder="Your name" />
              <input type="text" placeholder="Your Email" />
              <input type="text" placeholder="Subject" />
              <textarea placeholder="Message" />
            </div>
            <div className="contact-form-right">
            <iframe src={google_addr}  title="our location" style={{width:"100%", height:"100%", border:0}} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
            </div>
          </div>
          
        </div>
        <Footer />
  </>
  )
}

export default Contact;