import React from 'react'
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import SupportAgentOutlinedIcon from '@mui/icons-material/SupportAgentOutlined';
import ManageAccountsOutlinedIcon from '@mui/icons-material/ManageAccountsOutlined';
import LocalAtmOutlinedIcon from '@mui/icons-material/LocalAtmOutlined';

import '../styles/serviceCard.css';


function ServiceCard (){
  return(
    <div className="cardBox">
      <div className="card">
        <div className='card_img'>
          <LocalAtmOutlinedIcon style={{width: '200px', height: '40px'}}/>
        </div>
        <h2>Best Price Guarantee</h2>
        <p>Count on us to provide you with luxury at affordable prices.</p>
      </div>
      <div className="card">
        <div className='card_img'>
          <FavoriteBorderOutlinedIcon style={{width: '200px', height: '40px'}}/>
        </div>
        <h2>Travellers Love Us</h2>
        <p>We offer the creme de la creme and our travellers love us for it.</p>
      </div>
      <div className="card">
        <div className='card_img'>
          <ManageAccountsOutlinedIcon style={{width: '200px', height: '40px'}}/>
        </div>
        <h2>Best Travel Agent</h2>
        <p>We got you covered. We bring all your fantasies to reality and give you a life-long experience</p>
      </div>
      <div className="card">
        <div className='card_img'>
          <SupportAgentOutlinedIcon style={{width: '200px', height: '40px'}}/>
        </div>
        <h2>Our Dedicated Support</h2>
        <p>We offer a 24/7 support system to all our customers. Your satisfaction is our priority</p>
      </div>
    </div>
  )
}

export default ServiceCard;