import '../styles/pages-topsection.css';
import NavigationBar from './navigationBar';



function PagesTopSection(props) {
    return (
        <div id="topsection" style={{ backgroundImage:`url("${props.image}")`, backgroundSize: '100%' }}>
            <div id="navbar-container">
                <NavigationBar/>
            </div>
            <div id='topsection-body'>
                <div id="topsection-small"><span id="topsection-bold">HOME</span><span id='fade'>TOUR</span></div>
                <div id="topsection-big">{props.heading}</div>
            </div>
        </div>
    )
}

export default PagesTopSection;