import { HotelData } from "../data/hotelData";
import RatingIcon from '../components/rating-icon';
import "../styles/hotel.css";

const Hotel = () => {
  return(
    <div className="hotels">
      <h2>Popular Hotels and Rooms</h2>
      <div className="hotels-card-container">
        {HotelData.slice(0,4).map(e=>(
          <div className="hotels-cards">
            <img src={e.image} alt=""/>
            <div className="countries-price">
              <h2>{e.cityCountries}</h2>
              <p>{e.price}</p>
            </div>
            <div className="rating"><RatingIcon number= {e.ratings} className="ratings"/></div>
            <p className="ratings">ratings</p>
            <p>{e.description}</p>
            <p>{e.period}</p>
          </div>
        ))}
      </div>
    </div>
  )
}

export default Hotel;