import RatingIcon from '../components/rating-icon';
import TourCard from '../components/tourCard';
import '../styles/hotelPage.css';
import Footer from '../components/footer';
import PagesTopSection from '../components/pages-topsection';
import { Slider } from 'antd';
import image from '../assets/images/tour-header.jpg';

const TourPage = () => {

  const RangeSlide = () => (
    <Slider
      range={{
        draggableTrack: true,
      }}
      defaultValue={2000 | [2000, 500000]}
    />
    );

  return(
    <>
      <div>
        
        <PagesTopSection  heading={"Tour"} image={image} />
        
      </div>
      <div className="hotel-page-container">
        <div className='hotel-page-sidebar'>
          <div className="hotel-page-sidebar-top">
              <h3>FIND CITY</h3>
              <input type='text' placeholder='Destination, City' />
              <input type='select' placeholder='Select Location' />
              <input type='date' placeholder='Date From' />
              <input type='date' placeholder='Date To' />
              <RangeSlide />
              <div className='hotel-btn-search'>Search</div>
          </div>
          <div className="hotel-page-sidebar-bottom">
              <h3>STAR RATING</h3>
              <div className='rating'>
                <input type='checkbox'/>
                <RatingIcon number= {5} />
              </div>
              <div className='rating'>
                <input type='checkbox'/>
                <RatingIcon number= {4} />
              </div>
              <div className='rating'>
                <input type='checkbox'/>
                <RatingIcon number= {3} />
              </div>
              <div className='rating'>
                <input type='checkbox'/>
                <RatingIcon number= {2} />
              </div>
              <div className='rating'>
                <input type='checkbox'/>
                <RatingIcon number= {1} />
              </div>

          </div>
        </div>

        <div className="hotel-page-body">
          <TourCard />
        </div>
      </div>
      <Footer />
    </>
  );
}

export default TourPage