import '../styles/newsletter.css';

const Newsletter = () => {

  return(
    <div className="newsletter-container">
      <h2>Subscribe to our Newsletter</h2>
      <p className='newsletter-container-text'>We offer amazing discounts to our services. 
        We suggest special places, iconic places to explore and make memmories. 
        Subscribe to our weekly newsletter to not miss out on all these amazing deals.
      </p>
      
      <div className='newsletter-searchbox-container'>
            <input type='email' placeholder='Enter email address' />
            <div className='search-btn'>SEARCH</div>
      </div>
    </div>
  )
}

export default Newsletter;