import '../styles/funFacts.css';

const FunFacts = () => {

  const funData = [
    {
      numOfPlaces: "100,000",
      description: "Happy Customers"
    },
    {
      numOfPlaces: "40,000",
      description: "Destination Places"
    },
    {
      numOfPlaces: "87,000",
      description: "Hotels"
    },
    {
      numOfPlaces: "56,400",
      description: "Restaurant"
    },
  ]

  return(
    <div className="funFacts-container">
      <h2>Some fun facts</h2>
      <p className='funFacts-container-text'>More than 10,000 beautiful places to visit.</p>
      
      <div className='funFacts-details'>
        {
          funData.map(e=>(
            <p className='details'>{e.numOfPlaces}</p>
          ))
        }
        {
          funData.map(e=>(
            <p className='description'>{e.description}</p>
          ))
        }
      </div>
    </div>
  )
}

export default FunFacts;