import { RestuarantData } from "../data/restuarantData";
import RatingIcon from '../components/rating-icon';
import "../styles/restuarants.css";

const Restuarants = () => {
  return(
    <div className="restuarants">
      <h2>Popular Restuarants</h2>
      <div className="restuarants-card-container">
        {RestuarantData.slice(0,4).map(e=>(
          <div className="restuarants-cards">
            <img src={e.image} alt=""/>
              <h2>{e.restuarantName}</h2>
              <h4>{e.cityCountries}</h4>
            <div className="rating"><RatingIcon number= {e.ratings} className="ratings"/></div>
            <p className="ratings">ratings</p>
            <p>{e.description}</p>
            <p>{e.period}</p>
          </div>
        ))}
      </div>
    </div>
  )
}

export default Restuarants;