import "../styles/aboutUs.css"
import React from 'react';
import { Carousel } from 'antd';
import {useState} from "react";
import img1 from '../assets/images/Ijeoma.jpeg';
import img2 from '../assets/images/Chiamaka.jpeg';
import img3 from '../assets/images/Scholar.jpeg';
import img4 from '../assets/images/petshal.jpeg';
import img5 from '../assets/images/Pinky.jpeg';
import img6 from '../assets/images/Dora.jpg';

import Faqs from "../components/faqs";
import PagesTopSection from "../components/pages-topsection";
import Footer from '../components/footer';

import image from "../assets/images/about-header.jpg"


const contentStyle = {
  height: '400px',
  color: '#fff',
  lineHeight: '160px',
  textAlign: 'center',
  background: '#364d79',
  margin: '0px',
};

const data = [
  {tab:"What we do",title:'Offering Reliable Services', details:"We specialize in creating unforgettable travel experiences that cater to the wanderlust of every individual. As a premier travel and tour company, we offer a wide range of services designed to inspire, facilitate, and elevate your journeys around the world. With an in-depth understanding of global destinations, we provide expert guidance on choosing the perfect location for your next adventure. Our team's extensive knowledge ensures that you discover hidden gems, iconic landmarks, and authentic local experiences."},
  {tab:"Our mission",title:'Exceptional Travel Solutions', details:"At Demicod, our mission is to curate transformative journeys that transcend ordinary travel experiences. With a commitment to excellence, innovation, and personalization, we strive to craft immersive and authentic adventures that ignite a sense of wonder, foster cultural appreciation, and create lifelong memories for every traveler. Guided by our passion for exploration and a deep respect for the destinations we visit, we aim to promote sustainable and responsible tourism practices, while supporting local communities and preserving the natural and cultural heritage of the places we explore."},
  {tab:"Our goals",title:'Help Our Customer', details: "Our primary goal is to transform ordinary journeys into extraordinary memories. With a steadfast commitment to excellence, innovation, and customer satisfaction, also to provide personalized and attentive service to every traveler. We actively listen to our clients' desires, preferences, and feedback to ensure that each journey is tailored to their individual needs."},
]

const profileImage = [
  {image:img1, name: "Ojukwu Ijeoma", occupation: "MD/CEO"},
  {image:img2, name: "Chiamaka Okorie", occupation: "Tour Operator"},
  {image:img3, name: "Scholar Moemeke", occupation: "Travel Counselor"},
  {image:img4, name: "Petshal Maiwan  ", occupation: "Tour Director"},
  {image:img5, name: "Peace Ene Gabriel", occupation: "Local Tour Guide"},
  {image:img6, name: "Dorathy Williams", occupation: "Reservation Sales Agent"},
]

function AboutUsPage(props){
  const [currentTab,setCurrentTab] = useState(0)

  const change_tab=id=>{
    setCurrentTab(id)
  }

  return(
    <>
        <PagesTopSection heading="About Us" image={image}/> 
        <div id="about-us-container">

          <div id="about-us-image">
            {/* <h2>Meet our teams</h2> */}
          {/* <img src={img1} alt="Our Picture" width="100%" /> */}
             <Carousel autoplay>
                {profileImage.map((e,index)=>(
                    <>
                      {/* <img src={e.image} alt="Our potrait"  key={index}/><h3>{e.occupation}</h3> */}
                      <div id="about-us-team" style={{ backgroundImage:`url("${e.image}")`, backgroundSize: '100%' }}>
                        <p>{e.name}</p>
                        <p>{e.occupation}</p>
                      </div>
                      
                    </>
                )                     
                )}
                  
              </Carousel> 
          </div>

          <div id="about-us-info">
            <div id="about-us-info-head">
              {data.map((e,index)=>(
                <div className={currentTab===index?"about-us-child active" : "about-us-child"} onClick={()=>{change_tab(index)}}>
                  {e.tab}
                </div>
              ))}
            </div>

            <div id="about-us-info-child">
              <div id="info-title">{data[currentTab].title}</div>
              <div id="info-content">
              {data[currentTab].details}

              </div>
            </div>

          </div>
        </div>

{/* frequently asked section */}
        <div id="about-us-faqs">
          <h5>FAQS</h5>
          <h2><span>Frequently</span> Ask Question</h2>
          <Faqs />
        </div>
        <Footer />
    </>
  )
}


export default AboutUsPage

